// Mock error boundary
class MockErrorBoundary extends React.Component<
  { children: React.ReactNode },
  { hasError: boolean; error?: Error }
> {
  constructor(props: { children: React.ReactNode }) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error: Error) {
    return { hasError: true, error };
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    console.error('Error caught by boundary:', error, errorInfo);
  }

  render() {
    if (this.state.hasError) {
      return (
        <div data-testid="error-boundary" className="p-4 bg-red-50 border border-red-200 rounded">
          <h2 className="text-red-800 font-semibold mb-2">Âá∫Áé∞‰∫ÜÈîôËØØ</h2>
          <p className="text-red-600 mb-4">È°µÈù¢Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Âà∑Êñ∞ÈáçËØï</p>
          <button 
            data-testid="retry-button"
            onClick={() => this.setState({ hasError: false })}
            className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
          >
            ÈáçËØï
          </button>
        </div>
      );
    }

    return this.props.children;
  }
}

// Mock components with different states
